<template>
  <div id="distributionRegister">
    <Button type="primary" size="small" @click="getWord" :loading="exportWordLoading" style="position: absolute;right: 58px;top:8px"></Button>
    <h2></h2>
    <table border='0' cellpadding='0' cellspacing='0' id="tab">
      <tr>
        <td colspan='6' style="width: 25%"></td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{tabCon.redlineNum}}</td>
        <td colspan='6' style="width: 25%"></td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{tabCon.number}}</td>
      </tr>
      <tr>
        <td colspan='6'></td>
        <td colspan='18' style="font-weight: normal">{{tabCon.placeName}}</td>
      </tr>
      <tr>
        <td colspan='6'></td>
        <td colspan='18'>
          <div style="font-weight: normal;text-align: left;min-height: 106px">{{tabCon.content}}</div></td>
      </tr>
      <tr>
        <td colspan='6'></td>
        <td colspan='6' style="font-weight: normal">{{cacuLonLat(tabCon.proofLon)}}</td>
        <td colspan='6'></td>
        <td colspan='6' style="font-weight: normal">{{cacuLonLat(tabCon.proofLat)}}</td>
      </tr>
      <tr>
        <td colspan='4' rowspan='2'></td>
        <td colspan='20'></td>
      </tr>
      <tr>
        <td colspan='4'></td>
        <td colspan='4'></td>
        <td colspan='4'></td>
        <td colspan='4'></td>
        <td colspan='4'></td>
      </tr>
      <tr id="images">
        <td colspan='4' v-for="(item, index) in lmPhotos" :key="index">
          <div style="min-height: 120px;display: flex; justify-content: center;align-items: center;">
            <img :src="item.url" alt="" v-if="item.url !== ''"  @click="imgEnlarges()" style="width: 100%">
          </div>
        </td>
      </tr>
      <tr>
        <td colspan='6'></td>
        <td colspan='18'>{{tabCon.remarks}}</td>
      </tr>
      <tr>
        <td colspan='3'></td>
        <td colspan='3' style="font-weight: normal">{{tabCon.createUser}}</td>
        <td colspan='3'></td>
        <td colspan='3' style="font-weight: normal">{{renderTime(tabCon.createDate)}}</td>
        <td colspan='3'></td>
        <td colspan='3' style="font-weight: normal">{{tabCon.verifyPerson}}</td>
        <td colspan='3'></td>
        <td colspan='3' style="font-weight: normal">{{renderTime(tabCon.verifyDate)}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import { getDisById, disExportWord } from '@/api/acquisitionTable'
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.css'
import { cacuLonLat, renderTime } from '@/utils/common'
export default {
  name: 'distributionRegister',
  data () {
    return {
      /*  */
      tabCon: {
        lmBoardPhotos: []
      },
      exportWordLoading: false
    }
  },
  computed: {
    /*  */
    lmPhotos: function () {
      let arr = [1, 3, 2, 4, 5]
      let photosData = [{}, {}, {}, {}, {}, {}]
      console.log(this.tabCon.photos)
      for (let i in photosData) {
        for (let j in this.tabCon.photos) {
          if (this.tabCon.photos[j].type === 6) {
            photosData[0] = this.tabCon.photos[j]
          } else if (this.tabCon.photos[j].type !== 6 && this.tabCon.photos[j].type === arr[i]) {
            photosData[i * 1 + 1] = this.tabCon.photos[j]
          }
        }
        console.log(photosData[i])
        if (JSON.stringify(photosData[i]) === '{}') {
          photosData[i].url = ''
        }
      }
      console.log(photosData)
      return photosData
    }
  },
  props: {
    tabletId: {}
  },
  watch: {
    tabletId () {
      this.getLmboardById()
    }
  },
  mounted () {
    this.viewers = new Viewer(document.getElementById('images'))
  },
  methods: {
    /*  */
    getLmboardById () {
      getDisById({
        data: {
          id: this.tabletId
        }
      }).then((res) => {
        this.tabCon = res.data.data
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    lmMPositionsDate (lmMarkerRelationPositions) {
      if (lmMarkerRelationPositions.length < 3) {
        return lmMarkerRelationPositions.push({})
      } else {
        return lmMarkerRelationPositions.slice(0, 3)
      }
    },
    /*  */
    imgEnlarges () {
      this.viewers.update()
    },
    /*  */
    renderTime (date) {
      return renderTime(date)
    },
    /*  */
    cacuLonLat (e) {
      return cacuLonLat(e)
    },
    /* Word */
    getWord () {
      this.exportWordLoading = true
      disExportWord({
        data: {
          id: this.tabletId
        }
      }).then((res) => {
        this.exportWordLoading = false
        if (res.data.code === '0000') {
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data
          link.href = url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.exportWordLoading = false
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h2{
    padding:5px 0 0;
    font-weight: normal
  }
  #tab{
    border-collapse: collapse;
    table-layout: fixed;
    text-align: center;
    width: calc(100% - 16px);
    margin: 8px;
    border:solid #d1d1d1; border-width:1px 0 0 1px;
    td{
      padding: 5px;
      font-size: 12px;
      font-weight: bolder;
      border:solid #d1d1d1;
      border-width:0 1px 1px 0;
    }
  }
</style>
